const Login = (resolve) => require(['@/views/login/Login.vue'], resolve); //字典维护
const NotFound = (resolve) => require(['@/views/404.vue'], resolve); //字典维护
const Home = (resolve) => require(['@/views/Home.vue'], resolve); //字典维护

const maintainDic = (resolve) => require(['@/views/equipe/maintainDic/maintainDic.vue'], resolve); //字典维护
const baseInfo = (resolve) => require(['@/views/equipe/baseInfo/baseInfo.vue'], resolve); //基础信息
const query = (resolve) => require(['@/views/equipe/query/query.vue'], resolve); //设备查询
const connectInfo = (resolve) => require(['@/views/equipe/connectInfo/connectInfo.vue'], resolve); //连接信息管理
const log = (resolve) => require(['@/views/log/log.vue'], resolve); //设备日志管理

const cycle = (resolve) => require(['@/views/collectInfo/cycle/cycle.vue'], resolve); //周期
const task = (resolve) => require(['@/views/collectInfo/task/task.vue'], resolve); //任务
const info = (resolve) => require(['@/views/collectInfo/info/info.vue'], resolve); //数据

const adaptor = (resolve) => require(['@/views/collectInfoAdaptor/adaptor/adaptor.vue'], resolve); //适配器管理
const executePro = (resolve) => require(['@/views/collectInfoAdaptor/executePro/executePro.vue'], resolve); //数据采集执行程序
const realTimeVideo = (resolve) => require(['@/views/collectInfoAdaptor/realTimeVideo/realTimeVideo.vue'], resolve); //实时视频流
const realTimeInfo = (resolve) => require(['@/views/collectInfoAdaptor/realTimeInfo/realTimeInfo.vue'], resolve); //实时消息
const adaptorLog = (resolve) => require(['@/views/collectInfoAdaptor/adaptorLog/adaptorLog.vue'], resolve); //日志记录

const show = (resolve) => require(['@/views/statistical/show/show.vue'], resolve); //接入设备统计
const device = (resolve) => require(['@/views/statistical/device/device.vue'], resolve); //接入设备统计
const order = (resolve) => require(['@/views/statistical/order/order.vue'], resolve); //任务统计
const map = (resolve) => require(['@/views/statistical/map/map.vue'], resolve); //map

let routes = [{
    path: '/login',
    component: Login,
    name: '',
    hidden: true
  },
  {
    path: '/404',
    component: NotFound,
    name: '',
    hidden: true
  },
  {
    path: '/',
    component: Home,
    name: '智能设备管理',
    iconCls: 'el-icon-message', //图标样式class
    children: [{
        path: '/maintainDic',
        component: maintainDic,
        name: '设备字典',
        hidden: true
      },
      {
        path: '/baseInfo',
        component: baseInfo,
        name: '基础信息',
      },
      {
        path: '/query',
        component: query,
        name: '设备查询',
      },
      {
        path: '/connectInfo',
        component: connectInfo,
        name: '连接信息管理',
      },
      // {
      //   path: '/log',
      //   component: log,
      //   name: '生成设备日志管理',
      // },
    ]
  },
  {
    path: '/',
    component: Home,
    name: '采集信息管理',
    iconCls: 'fa fa-id-card-o',
    children: [{
        path: '/cycle',
        component: cycle,
        name: '采集周期管理'
      },
      {
        path: '/task',
        component: task,
        name: '采集任务管理'
      },
      {
        path: '/info',
        component: info,
        name: '采集数据管理'
      }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '采集程序适配器',
    iconCls: 'fa fa-address-card',
    children: [{
        path: '/adaptor',
        component: adaptor,
        name: '适配器管理'
      },
      {
        path: '/executePro',
        component: executePro,
        name: '数据采集执行程序'
      },
      // {
      //   path: '/realTimeVideo',
      //   component: realTimeVideo,
      //   name: '实时视频流'
      // },
      // {
      //   path: '/realTimeInfo',
      //   component: realTimeInfo,
      //   name: '实时消息'
      // },
      // {
      //   path: '/adaptorLog',
      //   component: adaptorLog,
      //   name: '日志记录'
      // }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '日志',
    iconCls: 'fa fa-bar-chart',
    children: [{
      path: '/log',
      component: log,
      name: '日志管理'
    }]
  },
  // {
  //   path: '/',
  //   component: Home,
  //   name: '对外接口服务(未)',
  //   iconCls: 'fa fa-bar-chart',
  //   children: [{
  //     path: '/echarts',
  //     component: echarts,
  //     name: '采集数据查询'
  //   }]
  // },
  {
    path: '/',
    component: Home,
    name: '查询统计',
    iconCls: 'fa fa-address-card',
    children: [
      {
        path: '/show',
        component: show,
        name: '统计'
      },
      {
        path: '/device',
        component: device,
        name: '接入设备统计'
      },
      {
        path: '/order',
        component: order,
        name: '任务统计'
      },
      {
        path: '/map',
        component: map,
        name: '设备地图'
      },
    ]
  },

  // {
  //   path: '/',
  //   component: Home,
  //   name: '导航一',
  //   iconCls: 'el-icon-message', //图标样式class
  //   children: [
  //     {
  //       path: '/form',
  //       component: Form,
  //       name: 'Form'
  //     },
  //     {
  //       path: '/user',
  //       component: user,
  //       name: '列表'
  //     },
  //   ]
  // },
  // {
  //   path: '/',
  //   component: Home,
  //   name: 'Charts',
  //   iconCls: 'fa fa-bar-chart',
  //   children: [{
  //     path: '/echarts',
  //     component: echarts,
  //     name: 'echarts'
  //   }]
  // },
  
  {
    path: '*',
    hidden: true,
    redirect: {
      path: '/404'
    }
  }
];

export default routes;



