import Mock from 'mockjs';

const Devices = [];

//厂家 设备型号 设备类型 注册日期 序列号 安装位置 机构 IP GIS
for (let i = 0; i < 50; i++) {
  Devices.push(Mock.mock({
    param1: '厂家',
    param2: '设备型号',
    param3: '设备类型',
    param4: '注册日期',
    param5: '序列号',
    param6: '安装位置',
    param7: '机构',
    param8: 'IP',
    param9: 'GIS',
  }));
}

export { Devices };



